.App {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #293d3d;
}

.Main {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #293d3d;

  .top-bar {
    display: flex;
    flex-direction: column;

    .bar {
      display: flex;
      justify-content: space-between;
      margin: 0 2rem;

      .logo {
        .img {
          height: 80px;
          margin-left: 50px;
        }
      }

      // .name:hover {
      //   color: white;
      // }

      .top-icons {
        display: flex;
        align-items: center;

        .login-register {
          display: flex;
          justify-content: space-between;
          width: 210px;

          .btn {
            font-size: 20px;
            background-color: #555;
            height: 50px;
            width: 100px;
            color: white;
            outline: none;
          }

          .btn:hover {
            background: rgb(214, 214, 214);
            color: #293d3d;
            cursor: pointer;
          }
        }
        .notification {
          background-color: #555;
          color: white;
          text-decoration: none;
          padding: 15px 26px;
          position: relative;
          display: inline-block;
          border-radius: 2px;
          height: 25px;
        }
        .notification:hover {
          background: darkgreen;
          cursor: pointer;
        }
      }
    }

    .links {
      .Nav {
        display: flex;
        flex-direction: row;
        justify-content: center;
        background-color: #d9d9d9;

        a {
          color: black;
          text-decoration: none;
          font-weight: bold;
        }

        a:hover {
          color: white;
        }

        font-size: 26px;

        .spacers {
          width: 5%;
        }
      }
    }
  }

  .page-item {
    display: flex;
    flex-direction: row;
    flex: 1;
    margin: auto;
    width: 100%;

    .page-content {
      display: flex;
      width: 100%;
      background-color: white;

      .sidebar-toggle {
        display: flex;
        flex-direction: column;
        justify-content: center;
        .arrow {
          cursor: pointer;
        }
        .arrow:hover {
          color: red;
        }
      }
    }

